$primaryColor: #4527a4;

.wp-block-b-blocks-test-purpose {
	box-sizing: border-box;

	* {
		box-sizing: border-box;
		// color: white;
	}


		/*! CSS Used from: https://pixellyo.com/nextai/html/assets/css/bootstrap.css */*,::after,::before

	ul
{
	padding-left:2rem
}
	ul
{
	margin-top:0;
	margin-bottom:1rem
}
.container-fluid
{
	--bs-gutter-x:1.5rem;
	--bs-gutter-y:0;
	width:100%;
	padding-right:calc(var(--bs-gutter-x) * .5);
	padding-left:calc(var(--bs-gutter-x) * .5);
	margin-right:auto;
	margin-left:auto
}
.row
{
	--bs-gutter-x:1.5rem;
	--bs-gutter-y:0;
	display:flex;
	flex-wrap:wrap;
	margin-top:calc(-1 * var(--bs-gutter-y));
	margin-right:calc(-.5 * var(--bs-gutter-x));
	margin-left:calc(-.5 * var(--bs-gutter-x))
}
.row>*
{
	flex-shrink:0;
	width:100%;
	max-width:100%;
	padding-right:calc(var(--bs-gutter-x) * .5);
	padding-left:calc(var(--bs-gutter-x) * .5);
	margin-top:var(--bs-gutter-y)
}

.col-12
{
	flex:0 0 auto;
	width:100%
}
.d-inline-block
{
	display:inline-block!important
}
.d-block
{
	display:block!important
}
.d-inline-flex
{
	display:inline-flex!important
}
.flex-shrink-0
{
	flex-shrink:0!important
}
.align-items-center
{
	align-items:center!important
}
.mb-4
{
	margin-block-end:1rem!important
}
.px-6
{
	padding-inline:1.5rem!important
}
.py-3
{
	padding-block:.75rem!important
}
.gap-2
{
	gap:.5rem!important
}
.gap-4
{
	gap:1rem!important
}
.fw-medium
{
	font-weight:500!important
}
// text color
.text-light
{
	--bs-text-opacity:1;
	color:rgba(var(--bs-light-rgb),var(--bs-text-opacity))!important
}
// background color
.bg-light
{
	--bs-bg-opacity:1;
	background-color:rgba(var(--bs-light-rgb),var(--bs-bg-opacity))!important
}
.bg-dark
{
	--bs-bg-opacity:1;
	background-color:rgba(var(--bs-dark-rgb),var(--bs-bg-opacity))!important
}
.rounded-3
{
	border-radius:var(--bs-border-radius-lg)!important
}
	/*! CSS Used from: https://pixellyo.com/nextai/html/assets/css/bootstrap-icons.css */.bi::before,[class*=" bi-"]::before
{
	display:inline-block;
	font-family:bootstrap-icons!important;
	font-style:normal;
	font-weight:normal!important;
	font-variant:normal;
	text-transform:none;
