	/*! CSS Used from: https://pixellyo.com/nextai/html/assets/css/style.css */.blend-x
{
	-webkit-mask: linear-gradient(90deg, transparent, rgba(109, 89, 89, 0.8) 25%, rgba(255, 255, 255, 0.8) 75%, transparent);
	mask: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.8) 25%, rgba(255, 255, 255, 0.8) 75%, transparent);
}
.list
{
	--direction:column;
	display:flex;
	flex-direction:var(--direction);
	margin:0;
	padding:0;
	list-style:none
}
.list-row
{
	--direction:row
}
.scroller-x
{
	overflow:hidden
}
.scroller-x[data-direction=right]
{
	--_animation-direction:reverse
}
.scroller-x[data-speed=slow]
{
	--_animation-duration:60s
}
.scroller-x__list
{
	width:-moz-max-content;
	width:max-content;
	flex-wrap:nowrap;
	animation:scroll var(--_animation-duration,60s) var(--_animation-direction,forwards) linear infinite
}

.scroller-x__list>li
{
	flex-shrink:0
}
.section-space-xsm-y
{
	padding-block-start:clamp(1rem,1.68vw + 1rem,2rem);
	padding-block-end:clamp(1rem,1.68vw + 1rem,2rem)
}
.section-space-top
{
	padding-block-start:clamp(3.75rem,6.5vw + 1rem,7.5rem)
}
.section-space-xsm-bottom
{
	padding-block-end:clamp(1rem,1.68vw + 1rem,2rem)
}
.container-max
{
	margin-inline:auto
}
.container{
	// background-color: #070707;
	// border: 1px solid red;
}
.icon svg{
	width: 20px;
    height: 100%;

}
.add-ticker-btn {
	width: 100%; /* Full width */
	padding: 1rem 2rem; /* Padding for vertical and horizontal spacing */
	font-size: 1.125rem; /* Larger text for readability */
	font-weight: 600; /* Slightly bold text */
	text-align: center; /* Center the text inside the button */
	color: white; /* White text color */
	background: linear-gradient(90deg, #4c6ef5,  #4c6ef5); 
	border: none; 
	border-radius: 0.75rem; 
	box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
	transition: transform 0.3s ease, background-color 0.3s ease; 
  }
  
  .add-ticker-btn:hover {
	background: linear-gradient(90deg, #3a53d9, #3a53d9);
	
  }
  
  .add-ticker-btn:focus {
	outline: none; 
	// box-shadow: 0 0 0 2px #7b45a4; 
  }
  
  
	@media (min-width:1600px)
{
.container-max
{
	max-width:97rem
}

}
	@media (min-width:1800px)
{
.container-max
{
	max-width:109.5rem
}

}
	@media (min-width:2100px)
{
.container-max
{
	max-width:128.25rem
}

}
	@media (min-width:2500px)
{
.container-max
{
	max-width:153.25rem
}

}
.bg-opacity-5
{
	--bs-bg-opacity:0.05
}
.text-opacity-70
{
	--bs-text-opacity:0.7
}
	@keyframes scroll
{
	to
{
	transform:translate(calc(-50% - .5rem))
}

}
	@font-face
{
	font-display:block;
